@mixin text_1 {
    font-family: 'Raleway';
    font-weight: 900;
    line-height: 75px;
    font-size: 64px;
    @media(width <= $md) {
        font-size: 48px;
    }
    @media(width <= $sm) {
        font-size: 36px;
    }
}

@mixin text_2 {
    font-family: 'Raleway';
    font-weight: 700;
    line-height: 42px;
    font-size: 36px;
}

@mixin text_3 {
    font-family: 'Raleway';
    font-weight: 700;
    line-height: 35px;
    font-size: 30px;
    @media(width <= $md) {
        font-size: 24px;
    }
    @media(width <= $sm) {
        font-size: 18px;
    }
}

@mixin text_4 {
    font-family: 'Raleway';
    font-weight: 900;
    line-height: 24px;
    font-size: 24px;
    @media(width <= $md) {
        font-size: 18px;
    }
}

@mixin text_5 {
    font-family: 'Raleway';
    font-weight: 700;
    line-height: 21px;
    font-size: 18px;
    @media(width <= $md) {
        font-size: 14px;
    }
}

@mixin text_6 {
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 0.8125rem;
    line-height: 15px;
    font-size: 13px;
    @media(width <= $md) {
        font-size: 12px;
    }
    @media(width <= $sm) {
        font-size: 13px;
    }
}

@mixin button($background-color) {
    width: 370px;
    height: 80px;
    display: block;
    margin: auto;
    background-color: $background-color;

    line-height: 80px;
    text-align: center;

    font-family: 'Raleway';
    font-weight: 900;
    font-size: 24px;
    color: #fff;

    animation-name: button_pulsing;
    animation-duration: 10s;
    animation-delay: 5s;
    animation-iteration-count: infinite;

    transition: 0.4s;

    &:hover {
        background: $button-hover-color;
    }
    &:active {
        background: $button-active-color;
        box-shadow: $button-shadow;
    }

    @media (width <= 414px) {
        width: calc(100% - 10px);
    }
}

@keyframes button_pulsing {
    0% {
        transform: none;
    }
    5%{
        transform: scale(1.1);
    }
    10% {
        transform: none;
    }
    15% {
        transform: scale(1.1);
    }
    20% {
        transform: none;
    }
}

@mixin navigation_underline($color) {
    &::after {
        content: '';
        display: block;
        position: absolute;
        margin-top: 40px;
        width: 100%;
        height: 3px;
        background: $color;
    }
}